import React from "react";
import "../../assets/css/login.css";
import authLayout from "../../hoc/authLayout";
import Button from "@restart/ui/esm/Button";
import axios from "axios";

class LoginPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      email: "",
      password: "",
    };

    this.handleEmailUpdate = this.handleEmailUpdate.bind(this);
    this.handlePasswordUpdate = this.handlePasswordUpdate.bind(this);
    localStorage.isAuthenticated = false;
  }

  handleEmailUpdate(event) {
    this.setState({ email: event.target.value });
  }

  handlePasswordUpdate(event) {
    this.setState({ password: event.target.value });
  }

  submitLogin() {
    const params = {
      email: this.state.email,
      password: this.state.password,
    };

    // if (
    //   this.state.email === process.env.REACT_APP_ADMIN_EMAIL &&
    //   this.state.password === process.env.REACT_APP_ADMIN_PASSWORD
    // ) {
    //   console.log("test");
    //   localStorage.isAuthenticated = true;
    //   window.location = "/user";
    // }

    axios
      .post(process.env.REACT_APP_API_URL + "login", params)
      .then((res) => {
        if (res.status === 200) {
          // when login success, currently redirect to empty page but we can add actions later

          localStorage.isAuthenticated = true;
          window.location = "/user";

          console.log("success");
        } else {
          this.setState({
            errors: { message: res.data.msg },
          });
        }
      })
      .catch((err) => {
        if (
          err.response.status === 401 ||
          err.response.status === 404 ||
          err.response.status === 403 ||
          err.response.status === 400
        ) {
          this.setState({
            errors: { message: err.response.data.msg },
          });
        }
        console.log("login data submit error: ", err);
      });
  }

  render() {
    return (
      <>
        <form className="login-form">
          <div className="d-flex align-items-center my-4">
            <h1 className="text-center fw-normal mb-0 me-3">Sign In</h1>
          </div>
          {/* <!-- Email input --> */}
          <div className="form-outline mb-4">
            <label className="form-label" htmlFor="form3Example3">
              Email address
            </label>
            <input
              type="email"
              id="form3Example3"
              className="form-control form-control-lg"
              placeholder="Enter a valid email address"
              onChange={this.handleEmailUpdate}
            />
          </div>

          {/* <!-- Password input --> */}
          <div className="form-outline mb-3">
            <label className="form-label" htmlFor="form3Example4">
              Password
            </label>
            <input
              type="password"
              id="form3Example4"
              className="form-control form-control-lg"
              placeholder="Enter password"
              onChange={this.handlePasswordUpdate}
            />
          </div>

          <div className="d-flex justify-content-between align-items-center">
            {/* <!-- Checkbox --> */}
            <div className="form-check mb-0">
              <input
                className="form-check-input me-2"
                type="checkbox"
                value=""
                id="form2Example3"
              />
              <label className="form-check-label" htmlFor="form2Example3">
                Remember me
              </label>
            </div>
            {/* <Link to="/reset-password" className="text-body">
              Forgot password?
            </Link> */}
          </div>

          <div className="text-center text-lg-start mt-4 pt-2">
            <Button
              className="btn btn-primary btn-lg"
              onClick={() => this.submitLogin()}
            >
              Login
            </Button>
            {/* <p className="small fw-bold mt-2 pt-1 mb-0">
              Don't have an account?{" "}
              <a href="#!" className="link-danger">
                Register
              </a>
            </p> */}
          </div>
        </form>
      </>
    );
  }
}

export default authLayout(LoginPage);
